<template>
  <div class="ct-container-menu">
    <div class="ct-menu-app flex" :class="{ 'ct-menu-active': active }">
      <div class="menu-app flex">
        <div class="ct-colorPick-component flex" v-if="getColorMenuOpen()">
          <div class="colorPick-component flex">
            <div class="ct-header-colorPick-component flex">
              <div class="header-colorPick-component flex">
                <div class="ct-title-header-colorPickComponent flex">
                  <p class="text-title-header-colorPickComponent">
                    Color Picker
                  </p>
                </div>
              </div>
            </div>
            <div class="ct-body-colorPick-component flex">
              <div class="body-colorPick-component flex">
                <Chrome
                  :style="{ width: '100%', height: '100%' }"
                  v-model="selectPickerColor"
                  @update="updateValue"
                ></Chrome>
              </div>
            </div>
            <div class="ct-footer-colorPick-component flex">
              <div
                class="footer-colorPick-component flex"
                @click="closeColorMenuOpen()"
              >
                <p class="text-footer-colorPick-component">OK</p>
              </div>
            </div>
          </div>
        </div>
        <div class="ct-header-menu-app flex">
          <div class="header-menu-app flex">
            <img :src="logo" class="left-header-menu-app flex" />
            <div class="right-header-menu-app flex">
              <p class="text-right-header-menu-app">{{ title }}</p>
            </div>
          </div>
        </div>
        <div class="ct-body-menu-app flex">
          <div class="body-menu-app flex">
            <div
              class="ct-item-menu-app flex"
              v-for="(item, key) in getActiveMenu()?.filter((i) => !i.hidden)"
              v-bind:key="key"
              @click="selectItem(item)"
            >
              <div class="item-menu-app flex" @click="changeCheckedItem(item)">
                <div class="ct-icon-menu flex">
                  <div class="icon-menu">
                    <fa
                      :icon="item.icon"
                      size="xl"
                      :style="{ color: '#5f6369' }"
                    />
                  </div>
                </div>
                <div
                  class="ct-name-menu flex"
                  v-if="
                    item.type !== 'input' &&
                    item.type !== 'checkbox' &&
                    item.type !== 'colorPick'
                  "
                >
                  <div class="name-menu flex">
                    <p class="text-name-menu">{{ item.name }}</p>
                  </div>
                </div>
                <div class="ct-input-box flex" v-if="item.type === 'input'">
                  <div class="input-box">
                    <div class="ct-title-input flex">
                      <div class="title-input flex">
                        <p class="text-title-input">{{ item.name }}</p>
                      </div>
                    </div>
                    <div class="ct-value-input flex">
                      <div class="value-input flex">
                        <input
                          class="input"
                          type="text"
                          :placeholder="locale('input_value')"
                          :value="item.data?.value || ''"
                          @change="onInputChange($event, item)"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  class="ct-checkbox-box flex"
                  v-if="item.type === 'checkbox'"
                >
                  <div class="checkbox-box">
                    <div class="ct-title-checkbox flex">
                      <div class="title-checkbox flex">
                        <p class="text-title-checkbox">{{ item.name }}</p>
                      </div>
                    </div>
                    <div class="ct-value-checkbox flex">
                      <div class="value-checkbox flex">
                        <svg
                          v-if="item.checked"
                          width="24"
                          height="17"
                          viewBox="0 0 24 17"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M8.56679 16.8247L0 8.79335L1.34594 7.35761L8.52263 14.0857L22.6084 0L24 1.39145L8.56679 16.8247Z"
                            fill="#0094FF"
                          />
                        </svg>
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  class="ct-color-pick flex"
                  v-if="item.type === 'colorPick'"
                >
                  <div class="color-pick flex">
                    <div class="ct-title-color-pick flex">
                      <div class="title-color-pick flex">
                        <p class="text-title-color-pick">{{ item.name }}</p>
                      </div>
                    </div>
                    <div class="ct-box-color-pick flex">
                      <div class="box-color-pick flex">
                        <div
                          class="selected-color-pick"
                          :style="{ background: item.selectedColor }"
                        ></div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="ct-item-menu-app flex"
              v-if="previousMenuId.length > 0"
              @click="goBack()"
            >
              <div class="item-menu-app flex">
                <div class="ct-icon-menu flex">
                  <div class="icon-menu">
                    <fa
                      :icon="'arrow-left'"
                      size="xl"
                      :style="{ color: '#5f6369' }"
                    />
                  </div>
                </div>
                <div class="ct-name-menu flex">
                  <div class="name-menu flex">
                    <p class="text-name-menu">{{ locale("go_back") }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="ct-footer-menu-app flex">
          <div class="footer-menu-app flex"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Menu } from "@/fakebackend/menuFake";
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { IMenu, IMenuStructure } from "@/interfaces/IMenu";
import { Chrome } from "@ckpack/vue-color";

export default defineComponent({
  name: "MenuApp",
  components: { Chrome },
  mixins: [NuiComponentMixin],
  data() {
    return {
      menu: Menu,
      title: "cDev Lab",
      active: false,
      selectPickerColor: "#fff",
      logo: "https://static.cdev.shop/resources/vehicle-shop/thumbnails/cdev.png",
      activeMenuId: "",
      previousMenuId: [] as string[],
    };
  },
  props: {
    name: {
      type: String,
    },
    locale: {
      type: Function,
      required: true,
    },
  },
  methods: {
    findActiveMenu(menu: IMenuStructure, id: string): IMenu[] | undefined {
      if (menu.id === id) {
        return menu.menu;
      }

      for (const item of menu.menu) {
        if (item.submenu) {
          const result = this.findActiveMenu(item.submenu, id);

          if (result) {
            return result;
          }
        }
      }
    },
    getActiveMenu() {
      return this.findActiveMenu(this.menu, this.activeMenuId);
    },
    updateValue(color: string) {
      if (!color) return;
      const find = this.getActiveMenu()?.find((item: IMenu) => item.colorMenu);
      if (!find) return;
      this.selectPickerColor = color;
      find.selectedColor = color;

      this.postNUI("select", {
        event: find.event,
        data: { ...find.data, value: color },
      });
    },
    changeCheckedItem(item: IMenu) {
      if (item.type !== "checkbox" && item.type !== "colorPick") return;
      if (item.type === "checkbox") {
        item.checked = !item.checked;
        this.postNUI("select", {
          event: item.event,
          data: { ...item.data, value: item.checked },
        });
      }
      if (item.type === "colorPick") {
        item.colorMenu = !item.colorMenu;
        this.postNUI("select", {
          event: item.event,
          data: { ...item.data, value: item.colorMenu },
        });
      }
    },
    getColorMenuOpen() {
      const find = this.getActiveMenu()?.find((item: IMenu) => item.colorMenu);
      if (!find) return;
      this.selectPickerColor = find.selectedColor || "#ffff";
      return find.colorMenu;
    },
    closeColorMenuOpen() {
      const find = this.getActiveMenu()?.find((item: IMenu) => item.colorMenu);
      if (!find) return;
      find.colorMenu = !find.colorMenu;
    },
    selectItem(item: IMenu) {
      if (item.type === "checkbox") return;

      if (item.submenu) {
        this.previousMenuId.push(this.activeMenuId);
        this.activeMenuId = item.submenu.id;

        if (item.event) {
          this.postNUI("select", { event: item.event, data: item.data || {} });
        }
      } else {
        this.postNUI("select", { event: item.event, data: item.data || {} });
      }
    },
    onInputChange(event: Event, item: IMenu) {
      const newValue = (event.target as HTMLInputElement).value;

      item.data.value = newValue;

      this.postNUI("select", {
        event: item.event,
        data: { ...item.data, value: newValue },
      });
    },
    goBack() {
      const previousId = this.previousMenuId.pop();

      if (previousId) {
        this.activeMenuId = previousId;
      }
    },
    handleNUI({ action, data }: { action: string; data: unknown }) {
      if (action === "setMenu") {
        const { menu } = data as { menu: IMenuStructure };
        this.previousMenuId = [];
        this.menu = menu;
        this.activeMenuId = menu.id;
      } else if (action === "updateMenu") {
        const { menu } = data as { menu: IMenuStructure };
        this.menu = menu;
      } else if (action === "setTitle") {
        const { title } = data as { title: string };
        this.title = title;
      } else if (action === "setVisible") {
        const { visible } = data as { visible: boolean };
        this.active = visible;
      } else if (action === "goBack") {
        this.goBack();
      } else if (action === "setLogo") {
        const { logo } = data as { logo: string };
        this.logo = logo;
      }
    },
    handleKeyDown(e: KeyboardEvent) {
      if (!this.active) return;

      if (e.key === "Escape") {
        const previousId = this.previousMenuId.pop();

        if (previousId) {
          this.activeMenuId = previousId;
        } else {
          this.postNUI("close", {});
        }
      }
    },
  },
  mounted: function () {
    window.addEventListener("keydown", this.handleKeyDown);
  },
  unmounted: function () {
    window.removeEventListener("keydown", this.handleKeyDown);
  },
  watch: {
    selectPickerColor: {
      handler() {
        if (typeof this.selectPickerColor === "string") return;
        this.updateValue((this.selectPickerColor as any).hex);
      },
      immediate: true,
    },
  },
});
</script>
<style></style>
