<script lang="ts">
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { IPremiumNotify, IPremiumTextUI } from "@/interfaces/IPremiumNotify";
import SvgComponent from "@/utils/svgComponent.vue";

export default defineComponent({
    name: "cDevAlert",
    components: { SvgComponent },
    mixins: [NuiComponentMixin],
    data() {
        return {
            notifyList: [] as IPremiumNotify[],
            textUIList: [] as IPremiumTextUI[],
            notificationId: 0,
            updateId: 0,
            config: {
                PlaySFX: true,
                SFXVolume: 0.3,
                NotifyPosition: 'right',
                NotifyPositionDistance: '2rem',
                NotifyPositionHeight: '4rem',
                NotifyGapDistance: '2rem',
                NotifySizeIcon: '2rem',
                NotifyTitleSize: '2rem',
                NotifyTitleColor: '#FFFFFF',
                NotifyDescSize: '1.5rem',
                NotifyDescColor: 'white',
                NotifyPhoneIconColor: '#FF8125',
                NotifyInfoIconColor: '#2562FF',
                NotifyErrorIconColor: '#FF2525',
                NotifyWarningIconColor: '#FFB525',
                NotifySuccessIconColor: '#25FF7C',
                TextUIPosition: 'left',
                TextUIPositionDistance: '2rem',
                TextUIPositionHeight: '47rem',
                TextUIGapDistance: '2rem',
                TextUISizeIcon: '2rem',
                TextUITitleSize: '1.6rem',
                TextUITitleColor: '#FFFFFF',
                TextUIBoxInputSize: '3rem',
            }
        };
    },
    props: {
        name: {
            type: String,
        },
    },
    methods: {
        getIconColor(item: IPremiumNotify) {
            return item.type === "phone" ? this.config.NotifyPhoneIconColor :
                    item.type === "info" ? this.config.NotifyInfoIconColor :
                    item.type === "error" ? this.config.NotifyErrorIconColor :
                    item.type === "warning" ? this.config.NotifyWarningIconColor :
                    this.config.NotifySuccessIconColor;
        },
        handleNUI({ action, data }: { action: string; data: unknown }) {
            if (action === "addNotification") {
                const { type, title, message, duration, sfx } = data as any;
                this.addNotification(type, title, message, duration, sfx);
            } else if (action == "openTextUI") {
                const { title, key, color } = data as any;
                this.openTextUI(title, key, color);
            } else if (action === "closeTextUI") {
                this.closeTextUI((data as any).key);
            } else if (action === "updateTheme") {
                this.config = data as any;
            }
        },
        openTextUI(title: string, key: string, color: string) {
            for (const item of this.textUIList) {
                if (item.key === key) {
                    item.title = title;
                    item.color = color;
                    item.isShowing = true;

                    return;
                }
            }

            this.textUIList.push({
                title,
                key,
                color,
                isShowing: true,
            });
        },
        closeTextUI(key: string) {
            for (const item of this.textUIList) {
                if (item.key === key) {
                    item.isShowing = false;
                    break;
                }
            }
        },
        addNotification(type: string, title: string, message: string, duration: number, playSfx = true) {
            const id = this.notificationId++;
            const notification: IPremiumNotify = {
                id,
                type,
                title,
                desc: message,
                isShowing: false,
                playSfx,
            };

            this.notifyList.push(notification);

            setTimeout(() => {
                const notification = this.notifyList.find((n) => n.id === id);

                if (notification) {
                    notification.isShowing = true;

                    if (playSfx && this.config.PlaySFX) {
                        const sfx = document.createElement("audio");

                        sfx.src = `sfx/notifypremium/${type}.ogg`;
                        sfx.volume = this.config.SFXVolume;
                        sfx.onended = () => {
                            sfx.remove();
                        }

                        sfx.play();
                    }

                    setTimeout(() => {
                        const notification = this.notifyList.find((n) => n.id === id);

                        if (notification) {
                            notification.isShowing = false;

                            setTimeout(() => {
                                const notification = this.notifyList.find((n) => n.id === id);

                                if (notification) {
                                    notification.cleanup = true;

                                    if (this.notifyList.find((n) => n.isShowing || !n.cleanup)) {
                                        return;
                                    }

                                    this.notifyList = [];
                                }
                            }, 5000);
                        }
                    }, duration);
                }
            }, 100);
        }
    },
    created() {
        // this.addNotification("success", "Invalid Name", "Please provide a valid name " + Math.random() * 1000000, 3000);
        // let x = 1;
        // let y = setInterval(() => {
        //     x++;
