        // }, 500);
        //setTimeout(() => this.addNotification("phone", "Invalid Name", "Please provide a valid name ", 100000), 1000);
        //this.addNotification("phone", "Invalid Name", "Please provide a valid name ", 100000);
        //this.addNotification("warning", "Invalid Name", "Please provide a valid name ", 100000);

        let x = ["success", "error", "warning", "info", "phone"];
        let z = ["Job Updated", "Invalid Name", "Wrong Way", "New Customer", "New Message"];
        let a = ["Your job was successfully updated.", "Please provide a valid name", "You are going on the wrong way", "A new customer has arrived at your store.", "You have received a new message from Alex."];
        let y = 0;

        // setInterval(() => {
        //     this.addNotification(x[y], z[y], a[y], 3000);

        //     y++;

        //     if (y === x.length) {
