        //this.openTextUI("Do a 360", "E", "#2562FF");
        //this.openTextUI("Hello this is a medium text", "E", "#2562FF");
        //this.openTextUI("Hello this is a big text testing with other letters can be correct in ascent kkkkkkkkkkkkkkkkkkkkkkkkkkkkkkk", "E", "#2562FF");
    },
});
