<template>
  <div class="ct-container-pagination flex">
    <div class="ct-pagination-app flex">
      <div class="pagination-app flex">
        <div
          class="ct-item-pagination flex"
          v-for="index in maxPage"
          v-bind:key="index"
          @click="changePage(index)"
        >
          <div
            class="item-pagination"
            v-bind:class="{ actualPage: index === selectedPage }"
          >
            <div class="ct-page-item-pagination flex">
              <div class="page-item-pagination flex">
                <p class="text-page-item-pagination">{{ index }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="ct-next-pagination flex" @click="changePage(selectedPage + 1)">
          <div class="next-pagination">
            <div class="ct-icon-next-pagination flex">
              <div class="icon-next-pagination">
                <svg
                  width="7"
                  height="10"
                  viewBox="0 0 7 10"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    clip-rule="evenodd"
                    d="M0 1.05427L1.08835 0L6.25 5L1.08835 10L0 8.94573L4.0733 5L0 1.05427Z"
                    fill="white"
                  />
                </svg>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";

export default defineComponent({
  name: "cDevPagination",
  components: {},
  mixins: [NuiComponentMixin],
  data() {
    return {};
  },
  props: {
    selectedPage: {
      type: Number,
    },
    maxPage: {
      type: Number,
    },
  },
  methods: {
    changePage(page: number) {
      this.$emit("changePage", Math.min(page, this.maxPage || 0));
    },
  },
});
</script>
<style></style>
