<template>
  <div class="ct-input-tagging">
    <div class="input-tagging">
      <div
        class="ct-text-input"
        contenteditable="true"
        role="textbox"
        ref="contentEditable"
        @paste="handlePaste"
      >
      </div>
      <div class="ct-text-placeholder" v-if="false">>Message</div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { ESuggestionMode } from "@/interfaces/IChat";

export default defineComponent({
  name: "cDevInputTagging",
  components: {},
  mixins: [NuiComponentMixin],
  data() {
    return {
      targetPosition: 0,
      targetWord: "",
      targetNode: null as Node | null,
      uniqueMentionId: 0,
    };
  },
  props: {
    visible: {
      type: Boolean,
    },
    allowMention: {
      type: Boolean,
    },
    messageHistory: {
      type: Array,
      required: true,
    },
    historyIndex: {
      type: Number,
      required: true,
    },
  },
  methods: {
    handlePaste(event: ClipboardEvent) {
      event.preventDefault();

      const text = event.clipboardData?.getData("text/plain");

      if (!text) {
        return;
      }

      document.execCommand("insertHTML", false, text);
    },
    shouldShowPlaceholder() {
      const div = this.$refs.contentEditable as HTMLDivElement;
      return div && (!div.textContent || div.textContent?.trim() === "");
    },
    addCommand(command: string) {
      let div = this.$refs.contentEditable as HTMLDivElement;
      div.innerText = command;

      this.$nextTick(() => {
        const range = document.createRange();
        const sel = window.getSelection();

        range.setStart(div, 1);
        range.collapse(true);

        sel?.removeAllRanges();
        sel?.addRange(range);
      });
    },
    addMention(mention: string, id: number) {
      let div = this.$refs.contentEditable as HTMLDivElement;
      let html = div.innerHTML;

      const mentionStart = html.indexOf(this.targetWord);
      const mentionEnd = mentionStart + this.targetWord.length;
      const mentionId = this.uniqueMentionId++;

      let styled = `<span data-id="${id}" data-uid="${mentionId}" class="span-selectedSuggestion isTag"><span class="span-marker-suggestion">@</span>${mention}</span>&nbsp;`;

      let newHtml = html.slice(0, mentionStart) + styled + html.slice(mentionEnd);

      div.innerHTML = newHtml;

      this.$nextTick(() => {
        const addedSpan = div.querySelector(`span[data-uid="${mentionId}"]`);
        const nextNode = addedSpan?.nextSibling as Node;

        if (!nextNode) {
          return;
        }

        const range = document.createRange();
        const sel = window.getSelection();

        range.setStart(nextNode, 1);
        range.collapse(true);

        sel?.removeAllRanges();
        sel?.addRange(range);
      });
    },
    handleInput(event: Event) {
      if (!this.visible) {
        return;
      }

      const selectionNode = window.getSelection()?.anchorNode;
      const target = selectionNode as Node;

      if (target.parentElement?.closest(".ct-text-input") === null) {
        this.$emit("updateMentions", "");
        return;
      }

      if (!target || !target.textContent) {
        this.$emit("updateMentions", "");
        return;
      }

      const eventTarget = event.target as HTMLDivElement;
      const fullText = eventTarget.textContent;
      const text = target.textContent;
      const caretPos = this.getCaretPosition(target);

      if (fullText?.startsWith("/")) {
        const fullCommand = fullText.slice(1, caretPos);

        this.$emit("updateMentions", fullCommand, ESuggestionMode.COMMAND, caretPos);

        return;
      }

      const startOfCurrentWord = text.lastIndexOf(" ", caretPos - 2) + 1;
      const isTypingMention =
        (
          text.slice(startOfCurrentWord, caretPos).match(/@\w+/) ||
          text.slice(0, caretPos).match(/@\w+/)
        ) && this.allowMention;

      this.targetNode = target;

      if (isTypingMention) {
        this.$emit("updateMentions", text.slice(startOfCurrentWord + 1, caretPos), ESuggestionMode.MENTION);
        this.targetPosition = startOfCurrentWord;
        this.targetWord = text.slice(startOfCurrentWord, caretPos);
      } else {
        this.$emit("updateMentions", "");
      }
    },
    handleKeyDown(event: KeyboardEvent) {
      if (!this.visible) {
        return;
      }

      const selectionNode = window.getSelection()?.anchorNode;
      const target = selectionNode as Node;
      const targetElement = target as Element;

      if (!target ||
        (targetElement instanceof Element && targetElement?.closest(".ct-text-input") === null) ||
        (!(targetElement instanceof Element) && target.parentElement?.closest(".ct-text-input") === null)) {
        return;
      }

      if (event.key === "Backspace" || event.key === "Delete") {
        return this.handleBackspace(event, target);
      } else if (event.key === "Enter") {
        event.preventDefault();
        this.submit();
      } else if (!(event.code.startsWith("Arrow") || event.code === "Enter" || event.code === "Tab" || event.code === "Backspace" || event.code === "Delete" || event.code === "Escape" || event.code === "Insert" || event.code === "Home" || event.code === "End" || event.code === "PageUp" || event.code === "PageDown" || event.code === "CapsLock" || event.code === "NumLock" || event.code === "ScrollLock")) {
        const span = target.parentElement?.closest(".span-selectedSuggestion");

        if (span) {
          event.preventDefault();
        }
      }
    },
    getInputHTML() {
      const div = this.$refs.contentEditable as HTMLDivElement;
      const html = div.innerHTML;

      return html;
    },
    updateInput(text: string) {
      const div = this.$refs.contentEditable as HTMLDivElement;
      div.innerHTML = text;

      this.$nextTick(() => {
        const range = document.createRange();
        const sel = window.getSelection();

        range.setStart(div, div.childNodes.length);
        range.collapse(true);

        sel?.removeAllRanges();
        sel?.addRange(range);
      });
    },
    handleBackspace(event: KeyboardEvent, target: Node) {
      const span = target.parentElement?.closest(".span-selectedSuggestion");

      if (span) {
        event.preventDefault();
        span.remove();
      }
    },
    getCaretPosition(div: Node) {
      let caretPos = 0;
      let selection = window.getSelection();
      if (selection && selection.rangeCount) {
        let range = selection.getRangeAt(0);
        let preCaretRange = range.cloneRange();
        preCaretRange.selectNodeContents(div);
        preCaretRange.setEnd(range.endContainer, range.endOffset);
        caretPos = preCaretRange.toString().length;
      }
      return caretPos;
    },
    setCaretPosition(div: Node, pos: number) {
      let range = document.createRange();
      range.setStart(div, pos);
      range.collapse(true);
      let selection = window.getSelection();
      if (selection) {
        selection.removeAllRanges();
        selection.addRange(range);
      }
    },
    handleMouseDown(event: MouseEvent) {
      if (!this.visible) {
        return;
      }

      this.$nextTick(() => {
        const editable = this.$refs.contentEditable as HTMLElement;
          editable.focus();
      });
    },
    submit() {
      const editable = this.$refs.contentEditable as HTMLElement;
      const mentions: number[] = [];

      if (editable.innerText.trim() === "") {
        return;
      }

      const mentionSpans = editable.querySelectorAll(".isTag");

      for (const span of mentionSpans) {
        const id = Number(span.getAttribute("data-id"));

        if (Number.isNaN(id)) {
          continue;
        }

        if (!mentions.includes(id)) {
          mentions.push(id);
        }
      }

      let fullMessage = editable.innerHTML;

      for (const span of mentionSpans) {
        const id = span.getAttribute("data-id");
        const text = span.textContent;

        if (!id || !text) {
          continue;
        }

        const mention = `<mention id="${id}">${text}</mention>`;

        fullMessage = fullMessage.replace(span.outerHTML, mention);
      }

      fullMessage = fullMessage.replace(/&nbsp;/g, " ").trim();

      this.$emit("addHistory", editable.innerHTML);
      this.$emit("submit", fullMessage, mentions);

      editable.innerHTML = "";
    },
    focusInput() {
      const editable = this.$refs.contentEditable as HTMLElement;
      editable.focus();
    }
  },
  mounted() {
    window.addEventListener("input", this.handleInput);
    window.addEventListener("keydown", this.handleKeyDown);
    window.addEventListener("mouseup", this.handleMouseDown);
  },
  unmounted() {
    window.removeEventListener("input", this.handleInput);
    window.removeEventListener("keydown", this.handleKeyDown);
    window.removeEventListener("mouseup", this.handleMouseDown);
  },
});
</script>
