<template>
  <div class="container">
    <menuAppComponentVue
      ref="menu"
      v-show="visibility.menu"
      :name="`menu`"
      @postNUI="postNUIMessage"
      :locale="getLocale"
    ></menuAppComponentVue>
    <alertAppComponentVue
      ref="alert"
      v-show="visibility.alert"
      :name="`alert`"
      @postNUI="postNUIMessage"
      :locale="getLocale"
    ></alertAppComponentVue>
    <notifyAppComponentVue
      ref="notify"
      v-show="visibility.notify"
      :name="`notify`"
      @postNUI="postNUIMessage"
    ></notifyAppComponentVue>
    <shopAppComponentVue
      ref="shop"
      v-show="visibility.shop"
      :name="`shop`"
      :visible="visibility.shop"
      @postNUI="postNUIMessage"
      :locale="getLocale"
    ></shopAppComponentVue>
    <petsAppComponentVue
      ref="pets"
      v-show="visibility.pets && !visibility.shop"
      :name="`pets`"
      :visible="visibility.pets"
      @postNUI="postNUIMessage"
      :locale="getLocale"
    ></petsAppComponentVue>
    <petsThinkingApp
      ref="petsThinking"
      v-show="visibility.petsThinking"
      :name="`petsThinking`"
      :visible="visibility.petsThinking"
      @postNUI="postNUIMessage"
    ></petsThinkingApp>
    <chatAppComponentVue
      ref="chat"
      v-show="visibility.chat"
      :name="`chat`"
      :visible="visibility.chat"
      @postNUI="postNUIMessage"
    ></chatAppComponentVue>
    <premiumNotifyAppComponentVue
      ref="premiumNotify"
      v-show="visibility.premiumNotify"
      :name="`premiumNotify`"
      :visible="visibility.premiumNotify"
      @postNUI="postNUIMessage"
    ></premiumNotifyAppComponentVue>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import menuAppComponentVue from "../src/components/menu/menu-app.vue";
import alertAppComponentVue from "../src/components/alert/alert-app.vue";
import notifyAppComponentVue from "../src/components/notify/notify-app.vue";
import shopAppComponentVue from "../src/components/shop/shop-app.vue";
import petsAppComponentVue from "../src/components/pets/pets-app.vue";
import petsThinkingApp from "./components/pets/pets-thinking-app.vue";
import chatAppComponentVue from "../src/components/chat/chat-component.vue";
import premiumNotifyAppComponentVue from "../src/components/notifypremium/notify-premium-app.vue";

export default defineComponent({
  name: "PanelMenu",
  components: {
    menuAppComponentVue,
    alertAppComponentVue,
    notifyAppComponentVue,
    shopAppComponentVue,
    petsAppComponentVue,
    petsThinkingApp,
    chatAppComponentVue,
    premiumNotifyAppComponentVue
  },
  data() {
    return {
      visibility: {
        menu: true, // always on
        alert: true, // always on
        notify: true, // always on
        shop: false,
        pets: false,
        petsThinking: true, // always on
        chat: false,
        premiumNotify: true,
      } as { [component: string]: boolean },
      localizer: {} as { [key: string]: string },
    };
  },
  methods: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    async postNUIMessage(
      component: string,
      event: string,
      data: any,
      callback: (resp: any) => any
    ) {
      callback(
        await new Promise((resolve) => {
          const requestOptions = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              data,
              component,
              event,
            }),
          };

          fetch("https://cdev_lib/message", requestOptions)
            .then((response) => {
              const dataResponse = response.json();
              resolve(dataResponse);
            })
            .catch(() => {
              console.warn(`cDev: Post failed: ${component}.${event}`);
            });
        })
      );
    },
    getLocale(key: string) {
      return this.localizer[key] || key;
    },
  },
  created: function () {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    window.addEventListener("message", (event: any) => {
      const { action }: { action: string } = event.data;

      if (action === "toggle") {
        const { component, value }: { component: string; value: boolean } =
          event.data;

        if (component && this.visibility[component] !== undefined) {
          this.visibility[component] = value;
        }
      } else if (action === "message") {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const { component, data }: { component: string; data: any } =
          event.data;

        if (component) {
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          const componentRef: any = this.$refs[component];
          if (componentRef) {
            componentRef.handleNUI(data);
          }
        }
      } else if (action == "setLocalizer") {
        const { localizer }: { localizer: { [key: string]: string } } =
          event.data;

        if (localizer) {
          this.localizer = localizer;
        }
      }
    });
  },
});
</script>
<style></style>
