            '0px 0px 2rem ' + stylesVariable['PopUpBackgroundShadow'],
        }">
          <div class="icon-box-message">
            <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
              <mask id="path-1-outside-1_90_90" maskUnits="userSpaceOnUse" x="0" y="0" width="28" height="28"
                fill="black">
