                fill="black" fill-opacity="0.15" mask="url(#path-1-outside-1_90_90)" />
            </svg>
          </div>
          <div class="box-has-notify" v-if="newMessageCount > 0">
            <div class="number-inside-notify" :style="{
              background: mentionsIndicator
                ? stylesVariable['PopUpIconMentionBackgroundColor']
                : stylesVariable['PopUpIconNewMessageBackgroundColor'],
            }">
              <p class="text-number-notify-chat">{{ newMessageCount }}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="ct-content-chat" v-if="activatedChat" :style="{ width: stylesVariable['MainContentChatWidth'] }">
        <div class="ct-content-message-chat">
          <div class="content-message-chat" :style="{ maxHeight: stylesVariable['MessageBodyChatHeight'] }" ref="chatBox">
            <div class="ct-message-box-item" v-for="(message, index) of getMessageHistory()" :key="index">
              <div class="message-box-item" :style="{
                background: stylesVariable['ChatboxBackgroundColor'],
              }">
                <div class="ct-icon-type-message">
                  <div class="icon-type-message" :style="{
                    background: getChannelValues(message.channel)?.background,
                    width: stylesVariable['IconTypeMessageSize'],
                    height: stylesVariable['IconTypeMessageSize'],
                  }" v-html="getIconFromChannel(message.channel)"></div>
                </div>
                <div class="ct-icon-id-message">
                  <div class="icon-id-message" :style="{
                    background: stylesVariable['MessageIDBackgroundColor'],
                    width: stylesVariable['IconIdMessageSize'],
                    height: stylesVariable['IconIdMessageSize'],
                  }">
                    <p class="id-message-text" :style="{
                      color: stylesVariable['MessageIDTextColor'],
                      fontSize:
                        message.id > 999
                          ? '1rem'
                          : stylesVariable['FontIdSizeMessage'],
                    }">
                      {{ message.id }}
                    </p>
                  </div>
                </div>
                <div class="ct-message-content-message">
                  <div class="message-content-message">
                    <p class="message-chat-text" :style="{
                      color: stylesVariable['MessageTextColor'],
                      textShadow: stylesVariable['MessageTextShadow'],
                      fontSize: stylesVariable['FontSizeMessage'],
                    }">
                      {{ message.author }}:
                      <span class="message-chat-text" v-html="message.message" :style="{
                        color: stylesVariable['MessageTextColor'],
                        fontSize: stylesVariable['FontSizeMessage'],
                      }"></span>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="ct-content-input-message">
          <div class="content-input-message" :style="{
            background: stylesVariable['InputBoxBackgroundColor'],
            border: '1px solid ' + stylesVariable['InputBoxBorderColor'],
            paddingBottom: displaySuggestions.length > 0 ? '1.5rem' : '0rem',
          }">
            <div class="ct-top-content-input-message" :style="{
              padding: stylesVariable['InputSizeMessage'],
            }">
              <div class="top-content-input-message">
                <div class="icon-input-message" :style="{
                  width: stylesVariable['IconSendInputSize'],
                  height: stylesVariable['IconSendInputSize'],
                }">
                  <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd"
                      d="M10.2411 2.09006L4.58114 7.7501C3.93114 7.3301 3.21109 7.0001 2.53109 6.6301C1.76109 6.2201 1.85122 5.10016 2.66122 4.81016L10.2411 2.09006ZM11.4613 3.71018L5.84115 9.33018L6.77108 11.1802C7.16108 11.9602 8.29116 11.9001 8.60116 11.0901L11.4613 3.71018ZM9.6412 0.180152C12.0612 -0.679848 14.3813 1.69006 13.4513 4.09006L10.4613 11.8202C9.52127 14.2402 6.16105 14.4002 4.99105 12.0802L3.84115 9.82017C3.75115 9.63017 3.60123 9.48011 3.42123 9.39011L1.58114 8.39011C-0.708859 7.16011 -0.458947 3.80015 1.99105 2.93015L9.6412 0.180152Z"
                      fill="#767676" />
                  </svg>
                </div>
                <div class="input-message-item">
                  <inputTagging @updateMentions="updateMentions" @submit="submitMessage" @addHistory="addHistory"
                    ref="inputTagging" :visible="activatedChat" :allowMention="getSelectedChannel()?.allowMention">
                  </inputTagging>
                </div>
              </div>
            </div>
            <div class="ct-divisor-input-message" v-if="displaySuggestions.length > 0"></div>
            <div class="ct-bottom-content-input-message" v-if="displaySuggestions.length > 0">
              <div class="bottom-content-input-message">
                <div class="ct-suggestion-list">
                  <div class="suggestion-list" ref="suggestionList">
                    <div class="ct-box-suggestion" v-for="(item, index) of displaySuggestions" :key="index"
                      @mouseenter="item.hovering = true" @mouseleave="item.hovering = false"
                      @click="setSelectedSuggestion(item)">
                      <div class="box-suggestion" :class="{ hoveringSuggestion: item.hovering }">
                        <div class="ct-title-suggestion">
                          <div class="title-suggestion">
                            <p class="text-title-suggestion">
                              {{ item.title
                              }}<span class="text-span-title-suggestion" v-for="(subItem, index) of item.values" :class="{
  'text-span-title-suggestion-hover':
    overParamIndex === index,
}" :key="index">[{{ subItem.name }}]</span>
                            </p>
                          </div>
                        </div>
                        <div class="ct-desc-suggestion" v-if="item.description">
                          <div class="desc-suggestion">
                            <p class="text-desc-suggestion">
                              {{
                                overParamIndex > -1 && item.values
                                ? item.values[overParamIndex]?.description
                                : item.description
                              }}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="ct-channel-chat">
          <div class="channel-chat">
            <div class="ct-box-channel" v-for="(channel, index) of channelsChat.filter((c) => c.enabled)" :key="index">
              <div class="box-channel" :class="{ 'selected-channel': channel.selected }" @mouseenter="
                channel.hovering = true;
              channel.hoverT = hoverT;
              " @mouseleave="channel.hovering = false" @click="changeSelectedChannel(channel)" :style="{
  background: channel.background,
  width: stylesVariable['IconChannelSize'],
  height: stylesVariable['IconChannelSize'],
}">
                <div class="icon-box-channel" v-html="getIconFromChannel(channel.name)"></div>
                <div class="ct-tool-tip-channel" v-if="channel.hovering">
                  <div class="tool-tip-channel">
                    <div className="tooltip-arrow-channel"></div>
                    <p className="text-tooltip-channel">{{ channel.name }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import {
  DefaultChannelChat,
  DefaultChatStyle,
  DefaultSuggestionChat,
  ESuggestionMode,
  IChat,
  IChatChannel,
  ISuggestionChat,
} from "@/interfaces/IChat";
import { fakeChat } from "@/fakebackend/chatFake";
import inputTagging from "@/utils/inputTagging.vue";
export default defineComponent({
  components: { inputTagging },
  mixins: [NuiComponentMixin],
  data() {
    return {
      message: "",
      messages: [] as IChat[],
      channelsChat: DefaultChannelChat,
      selectedSuggestion: undefined as ISuggestionChat | undefined,
      suggestion: [] as ISuggestionChat[], //DefaultSuggestionChat,
      receiveNotify: false,
      activeNotify: false,
      activatedChat: false,
      stylesVariable: DefaultChatStyle,
      mentionsIndicator: false,
      icons: {} as { [name: string]: string },
      messageHistory: [] as string[],
      historyIndex: 0,
      displaySuggestions: [] as ISuggestionChat[],
      overParamIndex: -1,
      peekTimeout: null as number | null,
      newMessageCount: 0,
      config: {
        MessageHistoryLimit: 25,
        MessageSizeLimit: 1000,
      },
    };
  },
  props: {
    visible: Boolean,
    name: String,
  },
  methods: {
    handleNUI(data: any) {
      switch (data.action) {
        case "open":
          this.open();
          break;
        case "close":
          this.close();
          break;
        case "addMessage":
          this.addMessage(
            data.data.message,
            data.data.mentioned,
            data.data.dontNotify
          );
          break;
        case "addIcon":
          this.addIcon(data.data.name, data.data.data);
          break;
        case "updateChannels":
          this.updateChannels(data.data.channels);
          break;
        case "updateConfig":
          this.updateConfig(data.data.config);
          break;
        case "addSuggestion":
          this.addSuggestion(data.data.suggestion);
          break;
        case "updateTheme":
          this.updateTheme(data.data.theme);
          break;
      }
    },
    updateTheme(theme: any) {
      this.stylesVariable = theme;
    },
    addSuggestion(suggestion: ISuggestionChat) {
      this.suggestion = this.suggestion.filter(
        (s) => s.title !== suggestion.title
      );
      this.suggestion.push(suggestion);
    },
    getMessageHistory() {
      return this.messages.slice(-this.config.MessageHistoryLimit);
    },
    updateConfig(config: any) {
      this.config = config;
    },
    updateChannels(channels: IChatChannel[]) {
      const selectedChannel = this.channelsChat.find((c) => c.selected);
      this.channelsChat = channels;
      if (selectedChannel) {
        const newSelectedChannel = this.channelsChat.find(
          (c) => c.name === selectedChannel.name
        );
        if (newSelectedChannel) {
          newSelectedChannel.selected = true;
        }
      }
    },
    addIcon(name: string, data: any) {
      this.icons[name] = data;
    },
    handleKeyDown(event: any) {
      if (!this.activatedChat) return;
      switch (event.code) {
        case "Escape":
          this.requestClose();
          break;
        case "Tab":
          event.preventDefault();
          this.selectActiveSuggestion();
          break;
        case "ArrowUp":
          event.preventDefault();
          this.selectPreviousSuggestion();
          break;
        case "ArrowDown":
          event.preventDefault();
          this.selectNextSuggestion();
          break;
      }
    },
    addHistory(message: string) {
      this.messageHistory.push(message);
      this.historyIndex = this.messageHistory.length - 1;
    },
    selectPreviousSuggestion() {
      if (this.displaySuggestions.length === 0) {
        const msg = this.messageHistory[this.historyIndex];
        let sub = false;
        if (msg === (this.$refs.inputTagging as any).getInputHTML()) {
          this.historyIndex--;
        } else {
          sub = true;
        }

        this.historyIndex = Math.max(0, this.historyIndex);

        (this.$refs.inputTagging as any).updateInput(this.messageHistory[this.historyIndex]);

        if (this.historyIndex === 0) {
          return;
        }

        if (sub) {
          this.historyIndex--;
        }
        return;
      }

      const selectedSuggestion = this.displaySuggestions.find((s) => s.hovering);

      if (selectedSuggestion) {
        const index = this.displaySuggestions.indexOf(selectedSuggestion);
        if (index === 0) {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[this.displaySuggestions.length - 1].hovering =
            true;
        } else {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[index - 1].hovering = true;
        }
      } else {
        this.displaySuggestions[this.displaySuggestions.length - 1].hovering =
          true;
      }
      this.$nextTick(() => {
        const suggestionList = this.$refs.suggestionList as HTMLElement;
        const selectedSuggestion = suggestionList.querySelector(
          ".hoveringSuggestion"
        ) as HTMLElement;
        if (selectedSuggestion) {
          selectedSuggestion.scrollIntoView();
        }
      });
    },
    selectNextSuggestion() {
      if (this.displaySuggestions.length === 0) {
        if (this.historyIndex === this.messageHistory.length - 1) {
          return;
        }

        this.historyIndex++;
        (this.$refs.inputTagging as any).updateInput(this.messageHistory[this.historyIndex]);
        return;
      }

      const selectedSuggestion = this.displaySuggestions.find((s) => s.hovering);

      if (selectedSuggestion) {
        const index = this.displaySuggestions.indexOf(selectedSuggestion);
        if (index === this.displaySuggestions.length - 1) {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[0].hovering = true;
        } else {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[index + 1].hovering = true;
        }
      } else {
        this.displaySuggestions[0].hovering = true;
      }
      this.$nextTick(() => {
        const suggestionList = this.$refs.suggestionList as HTMLElement;
        const selectedSuggestion = suggestionList.querySelector(
          ".hoveringSuggestion"
        ) as HTMLElement;
        if (selectedSuggestion) {
          selectedSuggestion.scrollIntoView();
        }
      });
    },
    requestClose() {
      this.postNUI("request-close", {});
    },
    changeSelectedChannel(channel: IChatChannel) {
      this.channelsChat.forEach((item: IChatChannel) => {
        item.selected = false;
        item.hovering = false;
      });
      const hoverT = Date.now();
      channel.selected = true;
      channel.hovering = true;
      channel.hoverT = hoverT;
      setTimeout(() => {
        if (channel.hoverT === hoverT) channel.hovering = false;
      }, 500);
      this.postNUI("select-channel", { channel: channel.name });
    },
    getSelectedChannel() {
      return this.channelsChat.find((item: IChatChannel) => item.selected);
    },
    cycleChannels() {
      const channels = this.channelsChat.filter(
        (item: IChatChannel) => item.enabled
      );
      const getSelectedChannel = channels.find(
        (item: IChatChannel) => item.selected
      );
      if (getSelectedChannel) {
        const index = channels.indexOf(getSelectedChannel);
        if (index === channels.length - 1) {
          this.changeSelectedChannel(channels[0]);
        } else {
          this.changeSelectedChannel(channels[index + 1]);
        }
      } else {
        this.changeSelectedChannel(channels[0]);
      }
    },
    addMessage(message: IChat, mentioned: boolean, dontNotify?: boolean) {
      message.message = message.message
        .replaceAll("<mention", "<span class='mention'")
        .replaceAll("</mention>", "</span>");
      this.messages.push(message);
      if (!this.activatedChat && !dontNotify) {
        if (mentioned) {
          this.mentionsIndicator = true;
        }
        this.newMessageCount++;
        this.peek();
      }
      this.$nextTick(() => {
        const chatBox = this.$refs.chatBox as HTMLElement;

        if (chatBox) {
          chatBox.scrollTop = chatBox.scrollHeight;
        }
      });
    },
    getIconFromChannel(channel: string) {
      const icon = this.getChannelValues(channel)?.icon;
      return icon ? this.icons[icon] : "";
    },
    getChannelValues(name: string) {
      const findChannel = this.channelsChat.find(
        (item: IChatChannel) => item.name === name
      );
      if (findChannel) return findChannel;
    },
    updateMentions(term: string, mode: ESuggestionMode, caretPos?: number) {
      if (term.length === 0) {
        this.displaySuggestions = [];
        this.overParamIndex = -1;
        return;
      }
      const fullTerm = term;
      if (mode === ESuggestionMode.COMMAND) {
        const terms = term.split(" ");
        term = terms[0];
        if (
          terms.length > 1 &&
          !this.suggestion.some(
            (item) => item.title.toLowerCase() === `/${term}`
          )
        ) {
          this.displaySuggestions = [];
          this.overParamIndex = -1;
          return;
        }
      }
      this.displaySuggestions = this.suggestion.filter((item) => {
        return (
          item.title.toLowerCase().includes(term.toLowerCase()) &&
          item.mode === mode
        );
      });

      // sort by matching start
      this.displaySuggestions.sort((a, b) => {
        const aStart = a.title.toLowerCase().replace("/", "").startsWith(term.toLowerCase());
        const bStart = b.title.toLowerCase().replace("/", "").startsWith(term.toLowerCase());
        const aIndex = a.title.toLowerCase().replace("/", "").indexOf(term.toLowerCase());
        const bIndex = b.title.toLowerCase().replace("/", "").indexOf(term.toLowerCase());

        if (aStart && !bStart) {
          return -1;
        } else if (!aStart && bStart) {
          return 1;
        } else if (aStart && bStart) {
          return aIndex - bIndex; // items that start with the term are sorted by their position in the title
        } else {
          return 0;
        }
      });
      this.displaySuggestions.forEach((item) => {
        item.hovering = false;
      });
      const first = this.displaySuggestions[0];
      if (first) {
        first.hovering = true;
      }
      if (mode === ESuggestionMode.COMMAND) {
        let paramIndex = caretPos
          ? fullTerm.substring(0, caretPos).split(" ").length - 1
          : 0;
        paramIndex = paramIndex - 1;
        this.overParamIndex = paramIndex;
      }
    },
    setSelectedSuggestion(item: ISuggestionChat) {
      if (!item) return;
      const inputTagging = this.$refs.inputTagging as any;
      if (item.mode === ESuggestionMode.COMMAND) {
        inputTagging.addCommand(item.title);
      } else {
        inputTagging?.addMention(item.title, item.id);
      }
      this.displaySuggestions = [];
    },
    selectActiveSuggestion() {
      const first = this.displaySuggestions.find((s) => s.hovering);
      const inputTagging = this.$refs.inputTagging as any;
      if (first) {
        if (first.mode === ESuggestionMode.COMMAND) {
          inputTagging.addCommand(first.title);
        } else {
          inputTagging?.addMention(first.title, first.id);
        }
        this.displaySuggestions = [];
      } else {
        this.cycleChannels();
      }
    },
    // PEEK
    peek() {
      if (this.peekTimeout) clearTimeout(this.peekTimeout);
      this.receiveNotify = true;

      this.peekTimeout = setTimeout(
        () => {
          this.receiveNotify = false;
          this.peekTimeout = null;
        },
        this.mentionsIndicator ? Number(this.stylesVariable["MentionPeekTime"]) * 1000 : Number(this.stylesVariable["MessagePeekTime"]) * 1000
      );
    },
    open() {
      this.activeNotify = true;
      this.activatedChat = true;
      this.displaySuggestions = [];
      this.mentionsIndicator = false;
      this.newMessageCount = 0;

      const focusOnNextTick = () => {
        this.$nextTick(() => {
          const inputTagging = this.$refs.inputTagging as any;

          if (!inputTagging) {
            console.warn("inputTagging not found, retrying...");
            return focusOnNextTick();
          }

          inputTagging.focusInput();

          const chatBox = this.$refs.chatBox as HTMLElement;
          chatBox.scrollTop = chatBox.scrollHeight;
        });
      };

      focusOnNextTick();
    },
    close() {
      this.activeNotify = false;
      this.activatedChat = false;
    },
    submitMessage(message: string, mentions: number[]) {
      this.postNUI("message", {
        message,
        mentions,
      });
    },
    handleMouseWheel(event: WheelEvent) {
      if (!this.activatedChat) return;
      const chatBox = this.$refs.chatBox as HTMLElement;
      if (event.deltaY > 0) {
        chatBox.scrollTop += 50;
      } else {
        chatBox.scrollTop -= 50;
      }
    },
  },
  mounted() {
    window.addEventListener("keydown", this.handleKeyDown);
    window.addEventListener("wheel", this.handleMouseWheel);
  },
  unmounted() {
    window.removeEventListener("keydown", this.handleKeyDown);
    window.removeEventListener("wheel", this.handleMouseWheel);
  },
  created: function () {
    //this.open();
    /*setTimeout(() => {
      this.newMessageCount++;
      this.peek();
    }, 500);
    setTimeout(() => {
      this.newMessageCount++;
      this.mentionsIndicator = true;
      this.peek();
    }, 2500);
    setTimeout(() => {
      this.open();
      setTimeout(() => {
        this.close();
      }, 1500);
    }, 5000);*/
  },
});
</script>
