const NUIComponentMixin = {
    methods: {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        postNUI: async function (command: string, data: any) {
            return await new Promise(res => {
                // eslint-disable-next-line @typescript-eslint/no-explicit-any
                (this as any).$emit("postNUI", (this as any).name, command, data, (nuiResp: any) => res(nuiResp));
            });
        },
        // eslint-disable-next-line @typescript-eslint/no-unused-vars, @typescript-eslint/no-explicit-any
        handleNUI: function(data: any) {
            // empty
        },
    }
}


export default NUIComponentMixin;